import Network from '../utils/network.js'
export default {
	getList(id,data,success,fail) {
		Network.setGetMessage('article/column/' + id,data,'加载中', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	getArticleTypes(data,success,fail) {
		Network.setGetMessage('column',data,'加载中', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	getArticleList(id,data,success,fail) {
		Network.setGetMessage('article/column/'+id,data,'加载中', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	detail(id, data,success,fail) {
		Network.setGetMessage('article/' + id,data,'加载中', function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	},
	pv(id,data,success,fail) {
		Network.setPost('article/pv/' + id,data, function (res) {
		  success(res)
		}, function (res) {
		  uni.showToast({
			title: res.message,
			icon: 'none',
			duration: 2000
		  })
		})
	}
};
